import React, {useEffect, useState} from 'react';
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'

export default function Login(props) {
  let history = useHistory();
  const register = ()=>{
    const name = document.getElementById('name').value
    const emailid = document.getElementById('emailid').value
    const password = document.getElementById('password').value
    const gender = document.getElementById('gender').value
    const phonenumber = document.getElementById('phonenumber').value
    if (!name){
      alert("Please Enter Name")
    }
    else if (!emailid){
      alert("Please Enter Email ID")
    }
    else if (!password){
      alert("Please Enter Password")
    }
    else if (!gender){
      alert("Please Enter Gender")
    }
    else if (!phonenumber){
      alert("Please Enter Phone Number")
    }
    else{
    var option = {
      method: 'POST',
      url: `${props.url}/users`,
      headers: {
        "Content-type": "application/json; charset=UTF-8"
      },
      data: JSON.stringify({
        "name": `${name}`,
        "emailid": `${emailid}`,
        "password": `${password}`,
        "gender": `${gender}`,
        "phonenumber": `${phonenumber}`,
        })

        };
      axios
          .request(option)
          .then(function(response) {
            alert("Successfully Registered\nRedirecting to Marketing Dashboard")
            localStorage.setItem("token", response.data.accessToken)
            console.log(response.data.accessToken)
            history.push("/")
          })
          .catch(function(err) {
            alert(err)
            console.log(err)
          });
    }
  }
  return (
    <>
    <br/>
    <div className="container my-2" >
    <h1>Register Here &rarr;</h1>
    <h4>Name</h4>
    <input className="form-control my-2" id="name" required />
    <h4>Email ID</h4>
    <input className="form-control my-2" id="emailid" required />
    <h4>Password</h4>
    <input className="form-control my-2" id="password" required />
    <h4>Gender</h4>
    <input className="form-control my-2" id="gender" placeholder="0 = Male & 1 = Female" required />
    <h4>PhNumber</h4>
    <input className="form-control my-2" id="phonenumber" required />
    <button className="btn btn-success" onClick={register} >Register</button><br/>
    <Link to="/login"><button className="btn btn-danger my-2" >Login Instead</button></Link>
    </div>
    </>
  )
}
