// Importing Packages
import React, { useState } from 'react';
import './style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
  BrowserRouter as Router,
  Route, Switch
} from "react-router-dom";
import Content from './Components/Content'
import Navbar from './Components/Navbar'
import Add from './Components/Add'
import Edit from './Components/Edit'
import Login from './Components/Login'
import Register from './Components/Register'

// Creating App function
export default function App() {
  const server_url = `${window.location.href}api` //http://localhost:5000/api
  const title = "Marketing Dashboard"
  return (
    <>
    <Router>
    <Switch>
      <Route exact path = "/">
          <>
          <Navbar title={title} />
          <Content url={server_url}/>
          </>
      </Route>
      <Route path="/add">
        <Navbar title={title}/>
        <Add url={server_url} />
      </Route>
      <Route path="/edit/:id">
        <Navbar title={title}/>
        <Edit url={server_url} />
      </Route>
      <Route path="/login">
        <Login url={server_url} />
      </Route>
      <Route path="/register">
        <Register url={server_url} />
      </Route>
    </Switch>
    </Router>
    
    </>
    )

}
    
