import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { useParams} from 'react-router-dom';

export default function Edit(props) {
  const { id } = useParams();
  const [data, setData] = useState([])
  const [name, setName] = useState('')
  const [role, setRole] = useState('')
  const [phone, setPhone] = useState('')
  const [email, setEmail] = useState('')
  const [myclass, setMyClass] = useState('')
  const [country, setCountry] = useState('')
  const [subjects, setSubjects] = useState('')
  const [requirements, setRequirements] = useState('')
  const [Callstat, setCallstat] = useState('')
  const [WAstat, setWAstat] = useState('')
  const [emailstat, setEmailstat] = useState('')
  const [action, setAction] =useState('Not selected')
  useEffect(()=>{
    fetch(`${props.url}/marketing/${id}`)
      .then(response=> response.json())
      .then(result => {
        setData(result.data)
        setName(result.data[0].name)
        setRole(result.data[0].role)
        setPhone(result.data[0].phone)
        setEmail(result.data[0].email)
        setMyClass(result.data[0].class)
        setCountry(result.data[0].country)
        setSubjects(result.data[0].subjects)
        setRequirements(result.data[0].requirements)
        setCallstat(result.data[0].Callstat)
        setWAstat(result.data[0].WAstat)
        setEmailstat(result.data[0].emailstat)
        setAction(result.data[0].action)
      })
      .catch(err => console.log(err))
  }, [])
  const addRecord = () => {
    const name = document.getElementById("name").value
    const role = document.getElementById("role").value
    const phone = document.getElementById("phone").value
    const email = document.getElementById("email").value
    const myclass = document.getElementById("class").value
    const country = document.getElementById("country").value
    const subjects = document.getElementById("subjects").value
    const requirements = document.getElementById("requirements").value
    const Callstat = document.getElementById("Callstat").value
    const WAstat = document.getElementById("WAstat").value
    const emailstat = document.getElementById("emailstat").value
    const action = document.getElementById("action").value

    var option = {
      method: 'PATCH',
      url: `${props.url}/marketing/${id}`,
      headers: {
        "Content-type": "application/json; charset=UTF-8"
      },
      data: JSON.stringify({
        "name": `${name}`,
        "role": `${role}`,
        "phone": `${phone}`,
        "email": `${email}`,
        "class": `${myclass}`,
        "country": `${country}`,
        "subjects": `${subjects}`,
        "requirements": `${requirements}`,
        "Callstat": `${Callstat}`,
        "WAstat": `${WAstat}`,
        "emailstat": `${emailstat}`,
        "action": `${action}`
        })

        };
        axios
          .request(option)
          .then(function(response) {
            alert("Successfully Updated Record!")
          })
          .catch(function(err) {
            alert(err)
          });

  }
  return (
    <div className="container">
    <br/>
      <input name="name" id="name" value={name} onChange={(e)=>{setName(e.target.value)}} className="form-control my-2" placeholder="Name" />
      <input name="role" id="role" value={role} onChange={(e)=>{setRole(e.target.value)}} className="form-control my-2" placeholder="Role" />
      <input name="phone" id="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} className="form-control my-2" placeholder="Phone" />
      <input name="email" id="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} className="form-control my-2" placeholder="Email" />
      <input name="class" id="class" value={myclass} onChange={(e)=>{setMyClass(e.target.value)}} className="form-control my-2" placeholder="Class" />
      <input name="country" id="country" value={country} onChange={(e)=>{setCountry(e.target.value)}} className="form-control my-2" placeholder="Country" />
      <input name="subjects" id="subjects" value={subjects} onChange={(e)=>{setSubjects(e.target.value)}} className="form-control my-2" placeholder="Subjects" />
      <input name="requirements" id="requirements" value={requirements} onChange={(e)=>{setRequirements(e.target.value)}} className="form-control my-2" placeholder="Requirements" />
      <input name="Callstat" id="Callstat" value={Callstat} onChange={(e)=>{setCallstat(e.target.value)}} className="form-control my-2" placeholder="Call Status" />
      <input name="WAstat" id="WAstat" value={WAstat} onChange={(e)=>{setWAstat(e.target.value)}} className="form-control my-2" placeholder="WA Status" />
      <input name="emailstat" id="emailstat" value={emailstat} onChange={(e)=>{setEmailstat(e.target.value)}} className="form-control my-2" placeholder="Email Status" />
      <select name="action" id="action" className="form-select my-2" value={action} onChange={(e)=>{setAction(e.target.value)}}>
        <option value="Not selected">Not selected</option>
        <option value="To be registered" >To be registered</option>
        <option value="Follow up" >Follow up</option>
        <option value="Call back later" >Call back later</option>
        <option value="Not interested" >Not interested</option>
        <option value="DND" >DND</option>
        <option value="Needed home tutor only" >Needed home tutor only</option>
        <option value="Fraud" >Fraud</option>
        <option value="Registered" >Registered</option>
      </select>
      <button className="btn btn-success" onClick={addRecord} >Edit</button>
      <Link to="/"><button className="btn btn-danger mx-2">Back to records</button></Link>
    </div>
  )
}
