import React, {useEffect, useState} from 'react';
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'

export default function Login(props) {
  let history = useHistory();
  const login =()=>{
    const email = document.getElementById("emailid").value
    const password = document.getElementById("password").value
    var option = {
      method: 'POST',
      url: `${props.url}/auth/login`,
      headers: {
        "Content-type": "application/json; charset=UTF-8"
      },
      data: JSON.stringify({
        "emailid": `${email}`,
        "password": `${password}`
        })

        };
        axios
          .request(option)
          .then(function(response) {
            alert("Logged In!\nRedirecting you to Marketing Dashboard")
            localStorage.setItem("token", response.data.accessToken)
            console.log(response.data.accessToken)
            history.push("/")
          })
          .catch(function(err) {
            alert(err)
            console.log(err)
          });
  }
  return (
    <>
    <br/>
    <div className="container my-2" >
    <h1>Login Here &rarr;</h1>
    <h4>Email ID</h4>
    <input className="form-control my-2" id="emailid" />
    <h4>Password</h4>
    <input className="form-control my-2" id="password" />
    <button className="btn btn-success" onClick={login} >Login</button>
    <br/>
    <Link to="/register"><button className="btn btn-primary my-2">Register Here</button></Link>
    </div>
    </>
  )
}
