import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

export default function Add(props) {
  const addRecord = () => {
    const token = localStorage.getItem("token")
    const name = document.getElementById("name").value
    const role = document.getElementById("role").value
    const phone = document.getElementById("phone").value
    const email = document.getElementById("email").value
    const myclass = document.getElementById("class").value
    const country = document.getElementById("country").value
    const subjects = document.getElementById("subjects").value
    const requirements = document.getElementById("requirements").value
    const Callstat = document.getElementById("Callstat").value
    const WAstat = document.getElementById("WAstat").value
    const emailstat = document.getElementById("emailstat").value
    const action = document.getElementById("action").value
    let inputs = document.querySelectorAll("input");
    var option = {
      method: 'POST',
      url: `${props.url}/marketing/`,
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-type": "application/json; charset=UTF-8"
      },
      data: JSON.stringify({
        "name": `${name}`,
        "role": `${role}`,
        "phone": `${phone}`,
        "email": `${email}`,
        "class": `${myclass}`,
        "country": `${country}`,
        "subjects": `${subjects}`,
        "requirements": `${requirements}`,
        "Callstat": `${Callstat}`,
        "WAstat": `${WAstat}`,
        "emailstat": `${emailstat}`,
        "action": `${action}`
        })

        };
        axios
          .request(option)
          .then(function(response) {
            alert("Successfully Added Record!")
            inputs.forEach(input => input.value = '');
          })
          .catch(function(err) {
            alert(err)
          });

  }
  return (
    <div className="container">
    <br/>
      <input name="name" id="name" className="form-control my-2" placeholder="Name" />
      <input name="role" id="role" className="form-control my-2" placeholder="Role" />
      <input name="phone" id="phone" className="form-control my-2" placeholder="Phone" />
      <input name="email" id="email" className="form-control my-2" placeholder="Email" />
      <input name="class" id="class" className="form-control my-2" placeholder="Class" />
      <input name="country" id="country" className="form-control my-2" placeholder="Country" />
      <input name="subjects" id="subjects" className="form-control my-2" placeholder="Subjects" />
      <input name="requirements" id="requirements" className="form-control my-2" placeholder="Requirements" />
      <input name="Callstat" id="Callstat" className="form-control my-2" placeholder="Call Status" />
      <input name="WAstat" id="WAstat" className="form-control my-2" placeholder="WA Status" />
      <input name="emailstat" id="emailstat" className="form-control my-2" placeholder="Email Status" />
      <select name="action" id="action" className="form-select my-2">
        <option selected>Not selected</option>
        <option value="To be registered" >To be registered</option>
        <option value="Follow up" >Follow up</option>
        <option value="Call back later" >Call back later</option>
        <option value="Not interested" >Not interested</option>
        <option value="DND" >DND</option>
        <option value="Needed home tutor only" >Needed home tutor only</option>
        <option value="Fraud" >Fraud</option>
        <option value="Registered" >Registered</option>
      </select>
      <button className="btn btn-success" onClick={addRecord} >Add</button>
      <Link to="/"><button className="btn btn-danger mx-2">Back to records</button></Link>
    </div>
  )
}
