import React, {useEffect, useState} from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css'
import paginationFactory from 'react-bootstrap-table2-paginator'
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css'
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import filterFactory, {textFilter} from 'react-bootstrap-table2-filter';
import 'react-bootstrap-table2-filter/dist/react-bootstrap-table2-filter.min.css';
import { Link, useHistory } from 'react-router-dom'
import axios from 'axios'


export default function Content(props) {
  let history = useHistory()
  const [data, setData] = useState([])
  const token = localStorage.getItem("token")
  const option = {
    method:'GET',
    url: `${props.url}`,
    headers: {
      Authorization: `Bearer ${token}`,
      "Content-type": "application/json; charset=UTF-8"
    },

  }
  useEffect(()=>{
    axios
      .request(option)
      .then(function(response) {
        setData(response.data.data)
      })
      .catch(function(err) {
        alert("Please login first")
        console.log(err)
        history.push("/login")
      });
  }, [])
  const columns = [
    {dataField:'regid', text: "Reg id", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'name', text: "Name", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'role', text: "Role", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'phone', text: "Phone", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'email', text: "Email", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'class', text: "Class", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'country', text: "Country", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'subjects', text: "Subjects", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'requirements', text: "Requirements", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'Callstat', text: "Call Stat", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'WAstat', text: "WA Stat", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'emailstat', text: "Email Stat", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'date', text: "Date", sort:true, filter: textFilter({placeholder: "Search"})},
    {dataField:'action', text: "Action", sort:true, filter: textFilter({placeholder: "Search"})}
  ]

  const pagination = paginationFactory({
    page: 1,
    sizePerPage: 5,
    lastPageText: '>>',
    firstPageText: '<<',
    nextPageText: '>',
    prePageText: '<',
    showTotal: true,
    alwaysShowAllBtns: true,
    onPageChange: function (page, sizePerPage){
      console.log('page', page);
      console.log('sizePerPage', sizePerPage);
    },
    onSizePerPageChange: function (page, sizePerPage){
      console.log('page', page);
      console.log('sizePerPage',sizePerPage)
    }

  })

  const deleteRow =(regid)=> {
    var option = {
      method: 'DELETE',
      url: `${props.url}/marketing/${regid}`
    }
        axios
          .request(option)
          .then(function(response) {
            window.location.reload();
          })
          .catch(function(err) {
            alert(err)
          });
  }
  const expandRow = {
    showExapandColumn: true,
    renderer: row => (
      <div className="container">
      <table className="table table-striped table-dark table-sm table-hover text-center">
      <tbody>
      <tr>
        <th scope="row">Reg ID</th>
        <td>{row.regid}</td>
        <th scope="row">Name</th>
        <td>{row.name}</td>
      </tr>
      <tr>
        <th scope="row">Role</th>
        <td>{row.role}</td>
        <th scope="row">Phone</th>
        <td>{row.phone}</td>
      </tr>
      <tr>
        <th scope="row">Email</th>
        <td>{row.email}</td>
        <th scope="row">Class</th>
        <td>{row.class}</td>
      </tr>
      <tr>
        <th scope="row">Country</th>
        <td>{row.country}</td>
        <th scope="row">Subjects</th>
        <td>{row.subjects}</td>
      </tr>
      <tr>
        <th scope="row">Requirements</th>
        <td>{row.requirements}</td>
        <th scope="row">Call Status</th>
        <td>{row.Callstat}</td>
      </tr>
      <tr>
        <th scope="row">WA Status</th>
        <td>{row.WAstat}</td>
        <th scope="row">Email Status</th>
        <td>{row.emailstat}</td>
      </tr>
      <tr>
        <th scope="row">Date</th>
        <td>{row.date}</td>
        <th scope="row">Action</th>
        <td>{row.action}</td>
      </tr>
      </tbody>
      </table>
      <Link to={`/edit/${row.regid}`}><button className="btn btn-info mx-2" >Edit</button></Link>
      <button className="btn btn-danger" onClick={()=>{deleteRow(row.regid)}}>Delete</button>

      </div>
    )
  }

  return (
    <>
    <BootstrapTable
     id="table-dashboard"
     keyField="regid"
     bootstrap4
     columns={columns}
     data={data}
     pagination={pagination} 
     filter={filterFactory()}
     expandRow={expandRow}
     noDataIndication={'No Results Found'}
     />
    <div className="container">
    <Link to="/add"><button className="btn btn-primary mx-2">Add</button></Link>
    <ReactHTMLTableToExcel  
      className="btn btn-info"  
      table="table-dashboard"  
      filename="MarketingDashboard"  
      sheet="Sheet"  
      buttonText="Export to excel" /> 
    </div>
    </>
  )
}
